<template>
  <div class="pannel-commodity-wrapper">
    <van-image lazy-load :src="pic" @click="onCommodityClick" />
    <div class="name-wrapper" v-if="name">
      <span>{{name}}</span>
    </div>
    <div class="price-wrapper">
      <span class="price" :class="{'red': originPrice}">{{price | rmb}}</span>
      <span class="origin-price" v-if="originPrice">{{originPrice}}</span>
      <van-tag type="danger" v-if="tag === 'coupon'" style="margin-left: 10px;">拼</van-tag>
    </div>
    <div class="tag-wrapper">
      <van-tag type="primary" v-if="tag=== 'seckill'">秒杀</van-tag>
      <van-tag type="primary" v-if="tag=== 'coupon'">团购</van-tag>
    </div>
  </div>
</template>

<script>
import { Image, Tag } from 'vant'

export default {
  name: 'PannelCommodity',

  components: {
    [Image.name]: Image,
    [Tag.name]: Tag
  },

  props: {
    tag: {
      type: String,
      required: false
    },
    pic: {
      type: String,
      required: true
    },
    price: {
      type: Number,
      required: true
    },
    id: {
      type: String,
      required: true
    },
    name: {
      type: String,
      required: false
    },
    originPrice: {
      type: Number
    }
  },

  methods: {
    onCommodityClick () {
      this.$router.push(`/commodity/${this.tag}/${this.id}`)
    }
  }
}
</script>

