    span {
      flex: 0 0 100%;
      text-align: center;
      font-weight: 300;
      word-break: break-all;
      width: 0; // flex布局下使用百分比width不生效（超出一行用省略号)
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }

  .price-wrapper {
    padding-top: 10px;
