<template>
  <div class="banner-wrapper">
    <van-swipe
      :autoplay="3000"
      :height="120"
    >
      <van-swipe-item
        v-for="(item, index) in list"
        :key="index"
      >
        <van-image
          fit="cover"
          :height="120"
          v-lazy
          :src="item.img"
          @click="jumpTo(item.link)"
        />
      </van-swipe-item>
    </van-swipe>
  </div>
</template>

<script>
import { Swipe, SwipeItem, Image } from 'vant'

export default {
  name: 'Banner',

  components: {
    [Swipe.name]: Swipe,
    [SwipeItem.name]: SwipeItem,
    [Image.name]: Image
  },

  props: {
    list: {
      type: Array,
      required: true
    }
  },

  data () {
    return {}
  },

  methods: {
    jumpTo (link) {
      this.$router.push(link)
    }
  }
}
</script>

<style lang="less" scoped>
  .banner-wrapper {
    img {
      width: 100%;
    }
  }
</style>
