import Vue from 'vue'
import Router from 'vue-router'

import layoutTabbar from '@/layout/tab-bar'

import Home from '@/views/home'
import Category from '@/views/category'
import Topic from '@/views/topic'
import Cart from '@/views/cart'
import My from '@/views/my'

import Search from '@/views/search'

Vue.use(Router)

const routerMap = [
  {
    path: '/',
    redirect: '/home',
    component: layoutTabbar,
    children: [
      {
        path: '/home',
        name: 'homepage',
        component: Home
      },
      {
        path: '/category',
        name: 'category',
        component: Category
      },
      {
        path: '/topic',
        name: 'topic',
        component: Topic
      },
      {
        path: '/cart',
        name: 'cart',
        component: Cart
      },
      {
        path: '/my',
        name: 'my',
        component: My
      }
    ]
  },
  {
    path: '/search',
    component: Search
  }
]

const router = new Router({
  routes: routerMap
})

export default router
